.portfolio {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 90vh;
  @media screen and (max-width: 480px) {
    height: 100vh;
  }

  .arrowleft {
    height: 60px;
    position: absolute;
    cursor: pointer;
    left: 150px;
    @media screen and (max-width: 480px) {
      left: 15px;
    }
  }
  .arrowright {
    height: 60px;
    position: absolute;
    cursor: pointer;
    right: 150px;
    @media screen and (max-width: 480px) {
      right: 15px;
    }
  }

  .slider {
    height: 350px;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-out;
    @media screen and (max-width: 480px) {
      height: 100vh;
      justify-content: center;
    }

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;
      

      .item {
        width: 700px;
        height: 100%;
        background-color: white;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        
        @media screen and (max-width: 480px) {
          width: 80%;
          height: 150px;
          margin: 15px 0;
        }

        .left {
          flex: 4;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;

          .leftContainer {
            width: 90%;
            height: 70%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .imgContainer {
              width: 50px;
              height: 50px;
              border-radius: 50%;
              background-color: rgb(245, 179, 155);
              display: flex;
              align-items: center;
              justify-content: center;
              @media screen and (max-width: 480px) {
                width: 20px;
                height: 20px;
              }

              img {
                width: 45px;
                height:45px;
                @media screen and (max-width: 480px) {
                  width: 15px;
                }
              }
            }
            h2 {
              font-size: 20px;
              @media screen and (max-width: 480px) {
                font-size: 13px;
              }
            }
            p {
              font-size: 13px;
              @media screen and (max-width: 480px) {
                display: none;
              }
            }
            span {
              font-size: 12px;
              font-weight: 600;
              text-decoration: underline;
              cursor: pointer;
            }
          }
        }
        .right {
          flex: 8;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;

          img {
            width: 400px;
            transform: rotate(-10deg);
          }
        }
      }
    }
  }
}
