.foot {
  width: 100%;
  height: 150px;
  bottom: 0;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  .footer {
    display: flex;
    flex-direction: row;
    .icons {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-top: 30px;
      margin-bottom: 30px;
      color: #6c63ff;
      overflow: hidden;
      padding-left: 10px;
      padding-right: 10px;
      cursor: pointer;
      @media screen and (max-width: 480px) {
        color: #6c63ff;
        overflow: hidden;
        padding-left: 5px;
        padding-right: 5px;
        padding-bottom: 15px;
        padding-top: 15px;
        cursor: pointer;
        margin-top: 15px;
        margin-bottom: 10px;
      }
    }
    .icons:hover{
      color:pink;

    }
  }
  .links {
    display: flex;
    flex-direction: row;
    color: #6c63ff;

    .navbar {
      color: #6c63ff;
      font-weight: bolder;
      justify-content: center;
      text-align: center;
      display: flex;
      align-items: center;
      text-decoration: none;
      font-size: 20px;
      margin-left: 20px;
      margin-right: 10px;
      width: 100px;
      border-radius: 5px;
      align-content: center;
      padding: 5px;
      
      @media screen and (max-width: 480px) {
        font-size: 10px;
        margin-left: 10px;
        margin-right: 5px;
        width: 50px;
        border-radius: 5px;
        align-content: center;
        padding: 3px;
      }
    }
    a:hover{
      color: pink;
    }
  }
}
