.scroll-up-button {
  background-color: crimson;
color: #6c63ff;
  border: none;
  border-radius: 10px;
  font-weight: 500;
  cursor: pointer;
  font-size: 20px;

  margin-top: 10px;
  position: fixed;
  bottom: 10px;
  right: 10px;
  margin-left: 250px;
  width: 50px;
  height: 50px;
  color: white;
  text-align: center;
  @media screen and (max-width: 480px) {
    height: 30px;
    width: 75px;
    font-size: 10px;
  }
}
